import React, {useState} from 'react'
import Layout from '../core/Layout'
import {isAuthenticated} from '../auth'
import { Link } from 'react-router-dom'
import { createCategory } from './apiAdmin'

const AddCategory = () => {
    const [name, setName] = useState('');
    const [error, setError] = useState(false);
    const [success, setSuccess] = useState(false);

    // desctructure use and token from localStorage 

    const {user, token} = isAuthenticated();

    const handleChange = (e) => {
        setError('')
        setName(e.target.value)
    }

    const clickSubmit = (e) => {
        e.preventDefault()
        setError('')
        setSuccess(false)
        // make request of api to create category 
        createCategory(user._id, token, {name})
        .then(data => {
            if(data.error) {
                setError(data.error)
            } else {
                setError('');
                setSuccess(true);
            }
        })

    }


    const newCategoryForm = () => (
        <form onSubmit={clickSubmit}>
            <div className="form-group">
                <label className="text-muted">Name</label>
                <input type="text" className="form-control" value={name} autoFocus onChange={handleChange} required />
            </div>
            <button className="btn btn-outline-primary">Create Category</button>

        </form>
    );

    const showSuccess = () => {
        if(success) {
            return <h3 className="text-success">{name} is created</h3>
        }
    }

    const showError = () => {
        if(error) {
            return <h3 className="text-danger">Category should be unique</h3>
        }
    }
    const goBack = () => (
        <div className="mt-5">
            <Link to="/shopping/admin/dashboard" className="text-warning">Back to dashboard</Link>
        </div>
    )

    
    return (
        <Layout title="Add New Category" description={`Good Day ${user.name}, Ready to Add?`}>
                <div className="col-md-8 offset-md-2">
                    {showSuccess()}
                    {showError()}
                    {newCategoryForm()}
                    {goBack()}
                </div>
        </Layout>
    )

}

export default AddCategory;