import React from 'react'
import { BrowserRouter, Switch, Route } from 'react-router-dom'

// pages 
import Signup from './user/Signup'
import Signin from './user/Signin'
import Home from './core/Home'
// import Menu from './core/Menu'
import PrivateRoute from './auth/PrivateRoute'
import Dashboard from './user/UserDashboard'
import AdminRoute from './auth/AdminRoute'
import AdminDashboard from './user/AdminDashboard'
import AddCategory from './admin/AddCategory'
import AddProduct from './admin/AddProduct'
import Shop from './core/Shop'
import Product from './core/Product'
import Cart from './core/Cart'
import Orders from './admin/Orders'
import Profile from './user/Profile'
import ManageProducts from './admin/ManageProducts'
import UpdateProduct from './admin/UpdateProduct'


const Routes = () => {
    return (
        <BrowserRouter>
        {/* <Menu /> */}
            <Switch>
                <Route path="/shopping" exact component={Home} />
                <Route path="/shopping/shop" exact component={Shop} />
                <Route path="/shopping/signin" exact component={Signin} />
                <Route path="/shopping/signup" exact component={Signup} />
                <PrivateRoute path="/shopping/user/dashboard" exact component={Dashboard} />
                <AdminRoute path="/shopping/admin/dashboard" exact component={AdminDashboard} />
                <AdminRoute path="/shopping/create/category" exact component={AddCategory} />
                <AdminRoute path="/shopping/create/product" exact component={AddProduct} />
                <Route path="/shopping/product/:productId" exact component={Product} />
                <Route path="/shopping/cart" exact component={Cart} />
                <AdminRoute path="/shopping/admin/orders" exact component={Orders} />
                <PrivateRoute path="/shopping/profile/:userId" exact component={Profile} />
                <AdminRoute path="/shopping/admin/products" exact component={ManageProducts} />
                <AdminRoute path="/shopping/admin/product/update/:productId" exact component={UpdateProduct} />
                
            </Switch>   
        </BrowserRouter>
    )
}

export default Routes;