import React, {Fragment} from 'react'
import {Link, withRouter} from 'react-router-dom'
import {signout, isAuthenticated} from '../auth'
import { itemTotal } from './cartHelpers'

const isActive = (history, path) => {
    if(history.location.pathname === path) {
        return {color: '#ff9000'}
    } else {
        return {color: '#ffffff'}
    }
}

const Menu = ({history}) => (
    <div>
        <ul className="nav nav-tabs bg-primary">
            <li className="nav-item">
                <Link className="nav-link" style={isActive(history, '/shopping/')}  to="/shopping">Home</Link>
            </li>
            <li className="nav-item">
                <Link className="nav-link" style={isActive(history, '/shopping/shop')} to="/shopping/shop">Shop</Link>
            </li>
            <li className="nav-item">
                <Link className="nav-link" style={isActive(history, '/shopping/cart')} to="/shopping/cart">Cart <sup><small className="cart-badge">{itemTotal()}</small></sup></Link>
            </li>
            {isAuthenticated() && isAuthenticated().user.role === 0 && (
                <li className="nav-item">
                <Link className="nav-link" style={isActive(history, '/shopping/user/dashboard')} to="/shopping/user/dashboard">Dashboard</Link>
            </li>
            )}
            {isAuthenticated() && isAuthenticated().user.role === 1 && (
                <li className="nav-item">
                <Link className="nav-link" style={isActive(history, '/shopping/admin/dashboard')} to="/shopping/admin/dashboard">Dashboard</Link>
            </li>
            )}
            {!isAuthenticated() && (
                <Fragment>
                    <li className="nav-item">
                    <Link className="nav-link" style={isActive(history, '/shopping/signin')} to="/shopping/signin">Signin</Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" style={isActive(history, '/shopping/signup')} to="/shopping/signup">Signup</Link>
                </li>
            
                </Fragment>
            )}
            {isAuthenticated() && (
                <li className="nav-item">
                <span className="nav-link" style={{cursor: 'pointer', color: '#ffffff'}} onClick={() => signout(() => {
                    history.push('/shopping')
                })}>Signout</span>
            </li>
            )}
        </ul>
    </div>
)

export default withRouter(Menu);