import React, { useState } from 'react'
import {Link, Redirect} from 'react-router-dom'
import ShowImage from './ShowImage';
import moment from 'moment'
import {addItem, updateItem, removeItem} from './cartHelpers'

const Card = ({product, showViewProductButton = true, showAddToCartButton = true, cartUpdate= false, showRemoveProductButton= false}) => {

    const [redirect, setRedirect] = useState(false)
    const [count, setCount] = useState(product.count)

    const showViewButton = (showViewProductButton) => {
        return (
            showViewProductButton && (
                <button className="btn btn-outline-primary mt-2 mr-2 mb-2">
                    View Product
                </button>
            )
        )
    }

    const addToCart = () => {
        addItem(product, () => {
            setRedirect(true)
        })
    }

    const shouldRedirect = redirect => {
        if(redirect) {
            return <Redirect to="/shopping/cart" />
        }
    }

    const showAddToCart = (showAddToCartButton) => {
        return  ( showAddToCartButton && (<button onClick={addToCart} className="btn btn-outline-warning mt-2 mb-2">
        Add to cart
    </button>) )
    }

    // const showRemoveButton = (showRemoveProductButton) => {
    //     return  ( showRemoveProductButton && (<a href="/shopping/cart" onClick={() => removeItem(product._id)} className="btn btn-outline-danger mt-2 mb-2">
    //     Remove Product
    // </a>) )
    // }

    const showRemoveButton = (showRemoveProductButton) => {
        return  ( showRemoveProductButton && (<a href="/shopping" onClick={() => removeItem(product._id)} className="btn btn-outline-danger mt-2 mb-2">
        Remove Product
    </a>) )
    }


    const showStock = (quantity) => {
        return quantity > 0 
        ? (<span className="badge badge-primary badge-pill">In Stock</span>) 
        : (<span className="badge badge-warning badge-pill">Out of stock</span>)
    }

    const handleChange = productId => event => {
        setCount(event.target.value < 1 ? 1 : event.target.value)
        if(event.target.value >= 1) {
            updateItem(productId, event.target.value)
        }
    }

    const showCartUpdateOptions = cartUpdate => {
        return cartUpdate && <div>
            <div className="input-group mb-3">
                <div className="input-group-prepend">
                    <span className="input-group-text">Adjust Quantity</span>
                </div>
                <input type="number" className="form-control" value={count} onChange={handleChange(product._id)} />
                {/* <a href="/shopping/cart" className="btn btn-small btn-primary">Update</a> */}
                <a href="/shopping" className="btn btn-small btn-primary">Update</a>
            </div>
        </div>
    }

    return (
        // <div className="col-md-4 mb-3">
            <div className="card">
                <div className="card-header name">{product.name}</div>
                <div className="card-body">
                    {shouldRedirect(redirect)}
                    <ShowImage item={product} url="product" />
                    <p className="lead mt-2">{product.description == null ? 'No Description' : product.description.substring(0,40)}...</p>
                    <p className="black-10">â‚¹ {product.price}</p>
                    <p className="black-9">Category: {product.category && product.category.name}</p>
                    <p className="black-8">Added On: {moment(product.createdAt).fromNow()}</p>
                    {showStock(product.quantity)} <br />
                    <Link to={`/shopping/product/${product._id}`}>
                        {showViewButton(showViewProductButton)}
                    </Link>
                    {showAddToCart(showAddToCartButton)}

                    {showRemoveButton(showRemoveProductButton)}
                    
                    {showCartUpdateOptions(cartUpdate)}
                </div>
            </div>
        // </div>
    )
} 


export default Card;